<template>
  <section class="users">
    <span class="s-h1">Our cheerful users</span>

    <div class="container">
      <div class="row">
        <div class="col-md-6 offset-md-3">
          <p class="users__top-text">Attention! Sorting users by registration date</p>
        </div>
      </div>
      <div class="row">

        <div class="col-md-4 col-sm-6" v-for="user in users" :key="user.id">
          <div class="users-item">
            <img src="../assets/img/photo-cover.png" alt="Photo" class="user-img">
            <span class="user-name">{{user.name}}</span>
            <p class="user-spec">{{user.position}}</p>
            <p class="user-email" v-b-tooltip.hover.bottom :title="user.email">{{emailSubstr(user.email)}}</p>
            <p class="user-number">{{user.phone}}</p>
          </div>
        </div>

      </div>
    </div>

    <button class="main-button" v-on:click="getUsers" v-if="showMore">Show more</button>
  </section>
</template>

<script>
export default {
  props: {
    newUserCreated: {
      type: Boolean,
    }
  },
  data: () => ({
    users: [],
    allUsers: [],
    userCount: 3,
    mountUsers: 6,
    showMore: true,
    windowWidth: 0,
    viewCount: 6
  }),
  methods: {
    emailSubstr(email) {
      if(email.length > 25) {
        return email.substring(0, 25) + "..."
      }
      else {
        return email
      }
    },

    async getUsers() {
      let len = this.allUsers.length

      if(len > this.viewCount) {
        for (let i = this.viewCount; i < this.viewCount + this.userCount; i++) {
          if(this.allUsers[len-i]) {
            this.users.push(this.allUsers[len-i])
          }
        }
        this.viewCount = this.viewCount + this.userCount
      }

      if(len <= this.viewCount) {
        this.showMore = false
      }
      
    },

    async mountedUser() {
      this.allUsers = await this.$store.dispatch('mountUsers')
      let len = this.allUsers.length
      for(let i = 1; i < this.mountUsers+1; i++) {
        this.users.push(this.allUsers[len-i])
      }
    },

    getWindowWidth(event) {
      this.windowWidth = document.documentElement.clientWidth;
    }
  },
  watch: {
    async newUserCreated() {
      this.allUsers = await this.$store.dispatch('mountUsers')
      this.users = []
      let len = this.allUsers.length
      for(let i = 1; i < this.mountUsers+1; i++) {
        this.users.push(this.allUsers[len-i])
      }
      this.showMore = true
      this.viewCount = 6
    }
  },
  async mounted() {
    this.$nextTick(function() {
      window.addEventListener('resize', this.getWindowWidth);

      this.getWindowWidth()
    })

    setTimeout(() => {
      if(this.windowWidth < 576) {
        this.userCount = 3
        this.mountUsers = 3
        this.viewCount = 3
      }
      else if(this.windowWidth < 768) {
        this.userCount = 2
      }

      this.mountedUser()
    }, 0)

    
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.getWindowWidth);
  }
}
</script>