import Vue from 'vue'
import Vuex from 'vuex'

import firebase from 'firebase/app'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  },
  mutations: {
  },
  actions: {
    async mountUsers({}) {
      try {
        const users = []
        const fbUsers = (await firebase.database().ref(`/users`).once('value')).val() || {}
        Object.keys(fbUsers).forEach(key => {
          users.push({
            name: fbUsers[key].name,
            email: fbUsers[key].email,
            phone: fbUsers[key].phone,
            position: fbUsers[key].position
          })
        })

        return users
      } catch (e) {
        console.log(e)
      }
    },

    async getUsers({}, {page, userCount}) {
      try {
        let nextUsers = []

        await fetch(`https://frontend-test-assignment-api.abz.agency/api/v1/users?page=${page}&count=${userCount}`)
          .then((response) => {
            return response.json();
          })
          .then((data) => {
            nextUsers = data
          })
          
          return nextUsers
      } catch (e) {
        console.log(e)
      }
    },

    async mountPositions() {
      try {
        let positions = []

        await fetch(`https://frontend-test-assignment-api.abz.agency/api/v1/positions`)
          .then((response) => {
            return response.json();
          })
          .then((data) => {
            positions = data.positions
          })

        return positions
      } catch (e) {
        console.log(e);
      }
    },

    async registerHandler({}, {name, email, phone, position}) {
      try {
        const newUser = await firebase.database().ref(`users`)
              .push({name: name, email: email, phone: phone, position: position})
        return newUser
      } catch (e) {
        console.log(e)
      }
    }
  },
  modules: {
  }
})
